.about-section {
  background: var(--bg-section);
  padding: 80px 0;
  color: var(--text);
  font-family: 'Inter', sans-serif;
}

.about-header {
  text-align: center;
  margin-bottom: 60px;
}

.section-title {
  font-family: 'Libre Baskerville', serif;
  font-size: 3.5rem;
  font-weight: 700;
}

.about-me-row {
  min-height: 200px;
  display: flex;
  text-align: center;
  justify-content: center;
  gap: 40px;
  margin-bottom: 50px;
}


.about-me-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: 500px;
  width: 100%;
  text-align: left;
}

.about-name {
  font-family: 'Libre Baskerville', serif;
  font-size: 2rem;
  font-weight: 700;
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  background: linear-gradient(135deg, var(--text) 0%, var(--accent-soft) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.about-location {
  font-size: 0.95rem;
  color: var(--text-light);
  margin: 0.5rem 0;
  display: flex;
  align-items: center;
  gap: 4px;
}

.about-title {
  font-size: 1.1rem;
  font-weight: 500;
  color: var(--accent-soft);
  margin: 0.5rem 0;
}

.about-bio {
  font-size: 1.1rem;
  line-height: 1.7;
  color: var(--text-light);
  margin: 1rem 0;
}

.interests {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
}

.interest-tag {
  background: rgba(51, 45, 130, 0.3);
  color: #6b63ff;
  padding: 8px 16px;
  border-radius: 12px;
  font-size: 0.9rem;
  font-weight: 500;
  border: 1px solid rgba(51, 45, 130, 0.5);
  font-family: 'Inter', sans-serif;
  transition: all 0.2s ease;
}

.interest-tag:hover {
  background: rgba(51, 45, 130, 0.5);
  color: white;
  transform: scale(1.05);
}

@media (max-width: 768px) {
  .about-section {
    padding: 60px 0;
  }

  .section-title {
    font-size: 2.5rem;
  }

  .about-me-row {
    flex-direction: column;
    gap: 20px;
  }

  .about-me-image {
    width: 200px !important;
    height: 200px !important;
    margin: 0 auto;
    /* Center image on smaller screens */
  }

  .about-me-content {
    max-width: 100%;
  }
}

@media (max-width: 480px) {
  .section-title {
    font-size: 2rem;
  }

  .about-me-image {
    width: 150px !important;
    height: 150px !important;
  }

  .about-name {
    font-size: 1.5rem;
  }

  .about-bio {
    font-size: 1rem;
  }
}